import React from 'react'

import { Grid, Card, List, Button, Segment, Table, Icon, Image, Menu, Popup } from 'semantic-ui-react'

import Loader from './../../../../../components/loader'
import Registro from './registro'
import Item from './item'
import Participante from './participante'

import DialogAta from './../../../containers/dialog-ata/cadastro.container'
import DialogExcluirAta from './../../../containers/dialog-ata/excluir.container'

export default (props) => {

    const {
        carregando, onDesvincular, onMigrar, onTransmitir, showParticipantes, showParticipanteResult
    } = props

    return (
        <Loader loading={carregando}>
            <Grid container>
                <Grid.Row>

                    <Grid.Column>
                        <Menu>
                            <Menu.Item>{props.ata.nome}</Menu.Item>

                            <DialogAta id={props.ata._id}>
                                <Menu.Item icon="edit" content="Editar" />
                            </DialogAta>

                            <Popup
                                key="item"
                                position="bottom center"
                                content="Criar um novo plano de ao na ata atual, com os assuntos pendentes e informativos."
                                trigger={
                                    <Menu.Item
                                        icon="browser"
                                        content="Nova reviso"
                                        onClick={() => { onMigrar(props.ata._id) }} />
                                } />

                            <Popup
                                key="participante"
                                position="bottom center"
                                content="Ao clicar enviar e-mail para os participantes adicionados, informando os detalhes da ata e os assuntos do plano de ao."
                                trigger={
                                    <Menu.Item
                                        icon="send"
                                        content="Enviar e-mail"
                                        onClick={() => { onTransmitir(props.ata._id) }} />
                                } />

                            <Menu.Menu position='right'>
                                <DialogExcluirAta id={props.ata._id} projetoId={props.projeto}>
                                    <Menu.Item icon="trash" content="Excluir" />
                                </DialogExcluirAta>

                            </Menu.Menu>

                        </Menu>
                    </Grid.Column>

                </Grid.Row>

                <Grid.Row>

                    <Grid.Column width={4}>
                        <Registro
                            ata={props.ata}
                        />
                        <br />
                        <Participante
                            onDesvincular={onDesvincular}
                            participantes={props.participantes}
                            participante={props.participante}
                            ata={props.ata}
                            projeto={props.projeto}
                            showParticipantes={showParticipantes}
                            showParticipanteResult={showParticipanteResult}
                        />
                    </Grid.Column>

                    <Grid.Column width={12}>
                        <Item
                            onMigrar={onMigrar}
                            itens={props.itens}
                            item={props.item}
                            ata={props.ata}
                            projeto={props.projeto}
                        />
                    </Grid.Column>

                </Grid.Row>
            </Grid>
        </Loader>
    )
}


