import axios from 'axios'

export function obter(id) {
    return axios.get('/ata/participante/obter/' + id)
}

export function obterTodos() {
    return axios.get('/ata/participante/obter-todos/')
}

export function obterTodosPorAta(ataId) {
    return axios.get('/ata/participante/obter-todos-ata/' + ataId)
}

export function obterTodosPorProjeto(projetoId) {
    return axios.get('/ata/participante/obter-todos-projeto/' + projetoId)
}

export function cadastrar(data) {
    return axios.put('/ata/participante/cadastrar', {
        projetoId: data.projetoId,
        nome: data.nome,
        email: data.email,
        area: data.area,
        empresa: data.empresa
    })
}

export function editar(data) {
    return axios.post('/ata/participante/editar/' + data.id, {
        nome: data.nome,
        email: data.email,
        area: data.area,
        empresa: data.empresa
    })
}

export function vincular(data) {
    return axios.post('/ata/participante/vincular/' + data.ataId + '/' + data.participanteId, {
        presente: data.presente
    })
}

export function desvincular(ataId, participanteId) {
    return axios.post('/ata/participante/desvincular/' + ataId + '/' + participanteId)
}



