import React from 'react'

import { Grid, List, Button, Menu, Segment, Icon, Popup, Label } from 'semantic-ui-react'

import DialogParticipante from './../../../containers/dialog-participante/container'
import SemImagem from './../../../../../components/image-user/sem-imagem.png'

export default ({
    onDesvincular, onTransmitir, participantes, participante, ata, projeto, showParticipantes, showParticipanteResult
}) => {

    let showButton;
    if (participantes && participantes.length > 5) {
        if (showParticipanteResult) {
            showButton = (
                <Button secondary attached='bottom' icon="caret up" content="Ocultar" onClick={showParticipantes} />
            )
        } else {
            showButton = (
                <Button secondary attached='bottom' icon="caret down" content="Mostrar todos" onClick={showParticipantes} />
            )
        }
    } else {
        null
    }

    return (
        <div>

            <Menu borderless attached="top" >
                <Menu.Item header content='Participantes' />
                <Menu.Menu position='right'>
                    <DialogParticipante projetoId={projeto} participantes={participantes} ataId={ata._id}>
                        <Menu.Item icon="user plus" />
                    </DialogParticipante>
                </Menu.Menu>
            </Menu>

            <Segment attached>
                {(participantes || []).map((p, i) => (
                    showParticipanteResult || i <= 4 ?
                        <div style={{ 'margin-top': '5px' }} key={p._id} image basic>
                            {p.presente ?
                                <Label color='blue'>
                                    {p.nome} {p.sobrenome}
                                    <Icon name='delete' onClick={() => { onDesvincular(ata._id, p._id) }} />
                                </Label>
                                :
                                <Label basic>
                                    {p.nome} {p.sobrenome}
                                    <Icon name='delete' onClick={() => { onDesvincular(ata._id, p._id) }} />
                                </Label>
                            }
                        </div>
                        : null
                ))}
            </Segment>
            {showButton}
        </div >
    )
}


